
.el-submenu__title {
  padding: 0 10px !important;
  height: 36px;
  line-height: 36px;
  background-color: #333;
  color: #999;
}
.el-submenu__title:hover {
    background-color: #6F6F6F;
    color: #fff;
}
.el-submenu.is-opened .el-submenu__title {
  color: #fff;
  background-color: #6F6F6F;
}
.el-tree .el-tree-node__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #999;
}
.el-tree .el-tree-node__content:hover {
    background-color: #6F6F6F;
    color: #fff;
}
.el-tree .el-tree-node__content .node-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: hidden;
    position: relative;
}
.el-tree .el-tree-node__content .node-item .node-label {
      width: calc(100% - 40px);
      display: inline-block;
      padding-right: 40px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
.el-tree .el-tree-node__content .node-item .node-control {
      position: absolute;
      right: 20px;
      top: 0;
}
.el-tree .el-tree-node.is-current .el-tree-node__content {
  background-color: #6F6F6F;
  color: #fff;
}
.el-icon-document {
  position: relative;
  width: 14px;
  height: 14px;
}
.el-icon-document input[type=file] {
    width: 100%;
    height: 100%;
    padding: 0;
    opacity: .000001;
    color: #777;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
}
.hidden {
  display: none;
}
.search-box.active .el-input {
  width: 152px;
  -webkit-transition: width 0.1s ease 0s;
  transition: width 0.1s ease 0s;
}
.search-box.active .el-input__inner {
  padding: 3px 2px;
}
.search-box .el-input {
  width: 0;
}
.search-box .el-input .el-input__inner {
    margin-top: 6px;
    padding: 3px 0;
    border: none;
    height: 32px;
    color: #fff;
    background-color: #989898;
}
.search-box .el-input .el-input__inner::-webkit-input-placeholder {
    color: #fff;
}



